___CSS_LOADER_EXPORT___.push([module.id, `/* Trend Graph Card */
.trend-graph-card {
  position: absolute;
  left: 370px; /* Moved left by half its width (200px) */
  width: 875px; /* Extended width to the right */
  height: 380px;
  background: white;
  border-radius: 0.75rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  border: 1px solid #e5e7eb;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.trend-graph-card:hover {
  box-shadow: 0 0 20px rgba(20, 184, 166, 0.8);
  transform: translateY(-2px);
}

/* Header */
.trend-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  background: linear-gradient(135deg, #f9fafb, #ffffff);
  border-bottom: 1px solid #e5e7eb;
  gap: 1rem;
}

.trend-header h3 {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #374151;
  flex: 1;
  text-align: center;
}

.trend-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.time-range-select {
  padding: 0.25rem 0.5rem;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  color: #374151;
  background: white;
  cursor: pointer;
  transition: border-color 0.2s;
}

.time-range-select:hover {
  border-color: #9ca3af;
}

.time-range-select:focus {
  outline: none;
  border-color: #14b8a6;
  box-shadow: 0 0 0 2px rgba(20, 184, 166, 0.1);
}

.trend-nav-btn {
  width: 32px;
  height: 32px;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  background: white;
  color: #6b7280;
  font-size: 1.25rem;
  font-weight: 300;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.trend-nav-btn:hover {
  background: #f9fafb;
  border-color: #9ca3af;
  color: #374151;
}

.trend-nav-btn:active {
  transform: scale(0.95);
}

/* Vibration button */
.vibration-btn {
  width: 32px;
  height: 32px;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  background: white;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.5rem;
}

.vibration-btn:hover {
  background: linear-gradient(135deg, #06b6d4 0%, #0891b2 100%);
  border-color: #06b6d4;
  color: white;
  transform: scale(1.05);
}

.vibration-btn.active {
  background: linear-gradient(135deg, #06b6d4 0%, #0891b2 100%);
  border-color: #06b6d4;
  color: white;
  box-shadow: 0 4px 12px rgba(6, 182, 212, 0.3);
}

.vibration-btn.active:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 16px rgba(6, 182, 212, 0.4);
}

.trend-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.75rem;
  font-weight: 500;
  color: #6b7280;
}

.status-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #10b981;
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

/* Canvas */
.trend-canvas {
  display: block;
  width: 100%;
  height: 200px;
  background: #ffffff;
}

/* Loading state */
.trend-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 250px;
  color: #6b7280;
  font-size: 0.875rem;
}

.trend-loading .spinner {
  width: 24px;
  height: 24px;
  border: 2px solid #e5e7eb;
  border-top-color: #14b8a6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 0.5rem;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Make legend text more readable - bolder version of line colors */
.recharts-legend-item-text {
  font-size: 0.75rem !important;
  font-weight: 600 !important;
  opacity: 1 !important; /* Full opacity for legend text */
}`, "",{"version":3,"sources":["webpack://./src/styles/trend-graph.css"],"names":[],"mappings":"AAAA,qBAAqB;AACrB;EACE,kBAAkB;EAClB,WAAW,EAAE,yCAAyC;EACtD,YAAY,EAAE,gCAAgC;EAC9C,aAAa;EACb,iBAAiB;EACjB,sBAAsB;EACtB,0CAA0C;EAC1C,yBAAyB;EACzB,gBAAgB;EAChB,iDAAiD;AACnD;;AAEA;EACE,4CAA4C;EAC5C,2BAA2B;AAC7B;;AAEA,WAAW;AACX;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,qBAAqB;EACrB,qDAAqD;EACrD,gCAAgC;EAChC,SAAS;AACX;;AAEA;EACE,SAAS;EACT,mBAAmB;EACnB,gBAAgB;EAChB,yBAAyB;EACzB,sBAAsB;EACtB,cAAc;EACd,OAAO;EACP,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,uBAAuB;EACvB,yBAAyB;EACzB,uBAAuB;EACvB,kBAAkB;EAClB,cAAc;EACd,iBAAiB;EACjB,eAAe;EACf,6BAA6B;AAC/B;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,6CAA6C;AAC/C;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,yBAAyB;EACzB,uBAAuB;EACvB,iBAAiB;EACjB,cAAc;EACd,kBAAkB;EAClB,gBAAgB;EAChB,eAAe;EACf,oBAAoB;EACpB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,mBAAmB;EACnB,qBAAqB;EACrB,cAAc;AAChB;;AAEA;EACE,sBAAsB;AACxB;;AAEA,qBAAqB;AACrB;EACE,WAAW;EACX,YAAY;EACZ,yBAAyB;EACzB,uBAAuB;EACvB,iBAAiB;EACjB,cAAc;EACd,eAAe;EACf,oBAAoB;EACpB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,6DAA6D;EAC7D,qBAAqB;EACrB,YAAY;EACZ,sBAAsB;AACxB;;AAEA;EACE,6DAA6D;EAC7D,qBAAqB;EACrB,YAAY;EACZ,6CAA6C;AAC/C;;AAEA;EACE,sBAAsB;EACtB,6CAA6C;AAC/C;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;EACX,kBAAkB;EAClB,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,UAAU;EACV,WAAW;EACX,kBAAkB;EAClB,mBAAmB;EACnB,yDAAyD;AAC3D;;AAEA;EACE;IACE,UAAU;EACZ;EACA;IACE,YAAY;EACd;AACF;;AAEA,WAAW;AACX;EACE,cAAc;EACd,WAAW;EACX,aAAa;EACb,mBAAmB;AACrB;;AAEA,kBAAkB;AAClB;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,aAAa;EACb,cAAc;EACd,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,yBAAyB;EACzB,yBAAyB;EACzB,kBAAkB;EAClB,kCAAkC;EAClC,oBAAoB;AACtB;;AAEA;EACE;IACE,yBAAyB;EAC3B;AACF;;AAEA,mEAAmE;AACnE;EACE,6BAA6B;EAC7B,2BAA2B;EAC3B,qBAAqB,EAAE,iCAAiC;AAC1D","sourcesContent":["/* Trend Graph Card */\n.trend-graph-card {\n  position: absolute;\n  left: 370px; /* Moved left by half its width (20