  padding: 0.75rem;
  border-top: 1px solid #e5e7eb;
  font-size: 10px;
  color: #9ca3af;
}

.sidebar-version {
  margin-bottom: 0.25rem;
}

.sidebar-copyright {
  opacity: 0.7;
}

/* Content Area */
.content {
  flex: 1;
  overflow: auto;
  background: #ffffff;
}

.content-inner {
  padding: 1.5rem;
  min-height: 100%;
}

/* Dashboard Styles */
.dashboard {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1rem;
}

.dashboard-card {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 1rem;
  transition: all 0.3s ease;
}

.dashboard-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.75rem;
}

.card-title {
  font-size: 13px;
  font-weight: 600;
  color: #374151;
}

.card-icon {
  color: #14b8a6;
  font-size: 18px;
}

.metric-value {
  font-size: 24px;
  font-weight: 600;
  color: #111827;
  margin-bottom: 0.25rem;
}

.metric-label {
  font-size: 11px;
  color: #6b7280;
}

/* Sensor Grid */
.sensor-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 0.75rem;
  margin-top: 0.75rem;
}

.sensor-item {
  background: #f0fdfa;
  border: 1px solid #ccfbf1;
  border-radius: 6px;
  padding: 0.75rem;
  text-align: center;
}

.sensor-value {
  font-size: 18px;
  font-weight: 600;
  color: #0d9488;
}

.sensor-label {
  font-size: 10px;
  color: #6b7280;
  margin-top: 0.25rem;
}

/* Status Indicators */
.status-online {
  color: #10b981;
}

.status-offline {
  color: #ef4444;
}

.status-warning {
  color: #f59e0b;
}

/* Alert styles */
.alert-item {
  background: #fef2f2;
  border-left: 3px solid #ef4444;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 12px;
}

.alert-warning {
  background: #fffbeb;
  border-left-color: #f59e0b;
}

.alert-info {
  background: #f0fdfa;
  border-left-color: #14b8a6;
}

/* Chart container */
.chart-container {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 1rem;
  margin-top: 1rem;
}

/* Iframe Container */
.iframe-container {
  position: relative;
  width: 100%;
  height: calc(100vh - 60px);
  background: #ffffff;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #e5e7eb;
}

.full-iframe {
  width: 100%;
  height: 100%;
  border: none;
  background: white;
}

.iframe-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.95);
  z-index: 10;
}

.spinner-container {
  text-align: center;
}

.spinner {
  border: 2px solid #e5e7eb;
  border-top-color: #14b8a6;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  animation: spin 1s linear infinite;
  margin: 0 auto 0.75rem;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.loading-text {
  color: #14b8a6;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.loading-subtext {
  color: #9ca3af;
  font-size: 11px;
}

/* Terminal Styles */
.terminal-container {
  background: #1e293b;
  border-radius: 8px;
  padding: 0.75rem;
  height: calc(100vh - 80px);
  overflow: hidden;
  border: 1px solid #e5e7eb;
}

#terminal {
  width: 100%;
  height: 100%;
}

/* Buttons */
button {
  background: #f3f4f6;
  color: #374151;
  border: 1px solid #e5e7eb;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

button:hover {
  background: #e5e7eb;
}

button.primary {
  background: #f0fdfa;
  color: #0d9488;
  border-color: #ccfbf1;
}

button.primary:hover {
  background: #ccfbf1;
}

button.danger {
  background: #fef2f2;
  color: #dc2626;
  border-color: #fecaca;
}

button.danger:hover {
  background: #fecaca;
}

/* Forms */
input, select, textarea {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  padding: 0.5rem;
  border-radius: 4px;
  font-size: 13px;
  color: #111827;
  width: 100%;
}

input:focus, select:focus, textarea:focus {
  outline: none;
  border-color: #14b8a6;
  box-shadow: 0 0 0 2px rgba(20, 184, 166, 0.1);
}

label {
  display: block;
  font-size: 12px;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.25rem;
}

/* Tables */
table {
  width: 100%;
  border-collapse: collapse;
  font-size: 13px;
}

th {
  background: #f9fafb;
  padding: 0.5rem;
  text-align: left;
  font-weight: 600;
  color: #374151;
  border-bottom: 1px solid #e5e7eb;
}

td {
  padding: 0.5rem;
  border-bottom: 1px solid #f3f4f6;
}

/* Loading States */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: #6b7280;
}

.loading-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  color: #9ca3af;
  font-size: 14px;
  min-height: 200px;
}

.weather-bar-placeholder,
.sidebar-placeholder {
  background: #f9fafb;
  animation: pulse 1.5s ease-in-out infinite;
}

/* App Footer */
.app-footer {
  width: 100%;
  border-top: 1px solid rgba(229, 231, 235, 0.5);
  padding: 0.25rem 0;
  background: rgba(249, 250, 251, 0.3);
  margin-top: auto;
}

.footer-content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  width: 100%;
}

.footer-copyright {
  color: #111827;
  font-weight: 200;
  font-size: 0.75rem;
}

.footer-brand {
  color: #fb923c;
  font-family: 'Cinzel Decorative', cursive;
  font-weight: 200;
  font-size: 0.875rem;
}

.footer-powered {
  color: #5eead4;
  font-weight: 300;
  font-size: 0.75rem;
}

.footer-logo-link {
  display: flex;
  align-items: center;
  transition: opacity 0.2s;
}

.footer-logo-link:hover {
  opacity: 0.8;
}

.footer-logo {
  width: 32px;
  height: 32px;
  object-fit: contain;
}

.footer-github {
  display: flex;
  align-items: center;
  color: rgba(17, 24, 39, 0.8);
  transition: color 0.2s;
  font-size: 1rem;
}

.footer-github:hover {
  color: #111827;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal-content {
  background: white;
  border-radius: 0.75rem;
  padding: 0;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem;
  border-bottom: 1px solid #e5e7eb;
}

.modal-header h3 {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
  margin: 0;
}

.modal-close-btn {
  background: transparent;
  border: none;
  font-size: 1.5rem;
  line-height: 1;
  color: #6b7280;
  cursor: pointer;
  padding: 0;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.375rem;
  transition: all 0.2s ease;
}

.modal-close-btn:hover {
  background: #f3f4f6;
  color: #111827;
}

.modal-body {
  padding: 1.5rem;
}

.modal-body label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.5rem;
}

.modal-body input {
  width: 100%;
  padding: 0.625rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  transition: all 0.2s ease;
}

.modal-body input:focus {
  outline: none;
  border-color: #14b8a6;
  box-shadow: 0 0 0 3px rgba(20, 184, 166, 0.1);
}

.modal-body input.input-error {
  border-color: #ef4444;
}

.modal-body input.input-error:focus {
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

.error-message {
  color: #ef4444;
  font-size: 0.75rem;
  margin-top: 0.5rem;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
  padding: 1.25rem;
  border-top: 1px solid #e5e7eb;
  background: #f9fafb;
  border-radius: 0 0 0.75rem 0.75rem;
}

.btn-secondary {
  padding: 0.5rem 1rem;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-secondary:hover:not(:disabled) {
  background: #f3f4f6;
  border-color: #9ca3af;
}

.btn-primary {
  padding: 0.5rem 1rem;
  background: #14b8a6;
  border: 1px solid #14b8a6;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: white;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-primary:hover:not(:disabled) {
  background: #0d9488;
  border-color: #0d9488;
}

.btn-primary:disabled,
.btn-secondary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Responsive */
@media (max-width: 768px) {
  .sidebar {
    width: 50px;
  }
  
  .nav-label,
  .sidebar-title,
  .sidebar-footer {
    display: none;
  }
  
  .nav-item {
    justify-content: center;
    padding: 0.5rem;
  }
  
  .weather-bar {
    font-size: 10px;
    padding: 0.25rem 0.5rem;
  }
  
  .dashboard {
    grid-template-columns: 1fr;
  }
